  .btn, a:active,
  button:active,
  .btn:active, a:focus,
  button:focus,
  .btn:focus, a:hover,
  button:hover,
  .btn:hover, a:visited,
  button:visited,
  .btn:visited {
    color: inherit;
    text-decoration: none;
    outline: none; }
  a[disabled],
